"use client"

import { QRCodeCanvas } from "qrcode.react"
import { Button } from "@/components/ui/button"
import { Download, Share2, Printer, Clock, MapPin, User, ShieldCheck } from "lucide-react"
import { useRef } from "react"
import { toast } from "sonner"
import { cn } from "@/lib/utils"

interface VisitorPassProps {
    visitor: {
        id: number
        fullName: string
        program?: string
        purpose?: string
        department?: string
        date: string
        timeIn: string
        photo?: string
        visitToken?: string
    }
}

export function VisitorPass({ visitor }: VisitorPassProps) {
    const passRef = useRef<HTMLDivElement>(null)

    const downloadPass = async () => {
        const html2canvas = (await import("html2canvas")).default
        if (passRef.current) {
            const canvas = await html2canvas(passRef.current, {
                scale: 2,
                useCORS: true,
                backgroundColor: null,
            })
            const url = canvas.toDataURL("image/png")
            const link = document.createElement("a")
            link.href = url
            link.download = `pass-${visitor.fullName.replace(/\s+/g, "-").toLowerCase()}.png`
            document.body.appendChild(link)
            link.click()
            document.body.removeChild(link)
            toast.success("Visitor pass downloaded")
        }
    }

    const qrValue = JSON.stringify({
        id: visitor.id,
        name: visitor.fullName,
        token: visitor.visitToken || `VISIT-${visitor.id}`
    })

    return (
        <div className="flex flex-col items-center gap-6">
            {/* The Pass Card */}
            <div
                ref={passRef}
                id="official-visitor-pass-card"
                className="relative w-[340px] bg-white rounded-2xl overflow-hidden"
                style={{
                    fontFamily: 'Inter, system-ui, sans-serif',
                    border: '1px solid #e2e8f0',
                    boxShadow: '0 25px 50px -12px rgba(0, 0, 0, 0.25)'
                }}
            >
                {/* Header/Banner - Explicit Blue for consistent professional look */}
                <div
                    style={{ backgroundColor: '#1e40af', borderBottom: '4px solid #fbbf24' }}
                    className="h-32 flex flex-col items-center justify-start pt-6 relative"
                >
                    <div className="absolute top-4 left-4 opacity-20">
                        <ShieldCheck className="text-white h-12 w-12" />
                    </div>
                    <div className="z-10 text-center">
                        <h2 className="text-white font-black text-xl tracking-[0.1em] drop-shadow-sm">VISITOR PASS</h2>
                        <div style={{ backgroundColor: '#fbbf24' }} className="h-px w-12 mx-auto my-1"></div>
                        <p style={{ color: '#dbeafe' }} className="text-[10px] font-bold uppercase tracking-widest px-4">
                            ICT Visitors Management System
                        </p>
                    </div>
                </div>

                {/* Content Area */}
                <div
                    style={{ background: 'linear-gradient(to bottom, #ffffff, #f8fafc)' }}
                    className="px-6 pt-16 pb-8 flex flex-col items-center"
                >
                    {/* Floating Identification Photo */}
                    <div className="absolute top-20 left-1/2 -translate-x-1/2 z-20">
                        <div
                            style={{ backgroundColor: '#e2e8f0', borderColor: '#ffffff', boxShadow: '0 20px 25px -5px rgba(0, 0, 0, 0.1)' }}
                            className="h-28 w-28 rounded-full border-4 overflow-hidden flex items-center justify-center"
                        >
                            {visitor.photo ? (
                                <img
                                    src={visitor.photo}
                                    alt={visitor.fullName}
                                    className="h-full w-full object-cover"
                                />
                            ) : (
                                <div className="flex flex-col items-center">
                                    <User style={{ color: '#94a3b8' }} className="h-10 w-10" />
                                    <span style={{ color: '#94a3b8' }} className="text-[8px] font-bold">NO PHOTO</span>
                                </div>
                            )}
                        </div>
                    </div>

                    {/* Primary Visitor Branding */}
                    <div className="text-center mt-8 mb-6 space-y-1">
                        <h3 style={{ color: '#1e293b' }} className="text-2xl font-black tracking-tight">{visitor.fullName.toUpperCase()}</h3>
                        <div
                            style={{
                                backgroundColor: '#eff6ff',
                                border: '1px solid #dbeafe',
                                color: '#1d4ed8',
                                borderRadius: '9999px'
                            }}
                            className="inline-block px-3 py-1 text-[10px] font-black uppercase tracking-widest"
                        >
                            {visitor.program || "OFFICIAL GUEST"}
                        </div>
                    </div>

                    {/* QR Authentication Section */}
                    <div className="relative mb-6">
                        <div
                            style={{ background: 'radial-gradient(circle, rgba(59,130,246,0.1) 0%, rgba(251,191,36,0.1) 100%)' }}
                            className="absolute -inset-2 blur-xl rounded-full opacity-50"
                        ></div>
                        <div
                            style={{
                                backgroundColor: '#ffffff',
                                border: '2px solid #f1f5f9',
                                boxShadow: '0 1px 2px 0 rgba(0, 0, 0, 0.05)',
                                borderRadius: '1rem'
                            }}
                            className="relative p-3 transition-transform hover:scale-105 duration-300"
                        >
                            {visitor.visitToken ? (
                                <QRCodeCanvas
                                    value={qrValue}
                                    size={120}
                                    level={"H"}
                                    includeMargin={false}
                                />
                            ) : (
                                <div
                                    style={{
                                        backgroundColor: '#f8fafc',
                                        border: '2px dashed #e2e8f0',
                                        borderRadius: '0.75rem'
                                    }}
                                    className="w-[120px] h-[120px] flex flex-col items-center justify-center px-2 text-center"
                                >
                                    <ShieldCheck style={{ color: '#94a3b8' }} className="h-8 w-8 mb-1 opacity-20" />
                                    <p style={{ color: '#94a3b8' }} className="text-[10px] font-bold uppercase leading-tight">Verification<br />Pending</p>
                                </div>
                            )}
                        </div>
                    </div>

                    {/* Informational Data Grid */}
                    <div style={{ borderTop: '1px solid #e2e8f0' }} className="w-full grid grid-cols-2 gap-y-5 gap-x-4 pt-6">
                        <div className="space-y-1">
                            <p style={{ color: '#94a3b8' }} className="text-[10px] font-black uppercase tracking-tighter">Purpose of Visit</p>
                            <p style={{ color: '#334155' }} className="text-sm font-bold leading-tight">
                                {visitor.purpose || "Official Inquiry"}
                            </p>
                        </div>
                        <div className="space-y-1 text-right">
                            <p style={{ color: '#94a3b8' }} className="text-[10px] font-black uppercase tracking-tighter">Check-in Time</p>
                            <div className="flex items-center justify-end gap-1.5">
                                <Clock style={{ color: '#2563eb' }} className="h-4 w-4" />
                                <p style={{ color: '#2563eb' }} className="text-sm font-black">{visitor.timeIn}</p>
                            </div>
                        </div>
                        <div className="space-y-1">
                            <p style={{ color: '#94a3b8' }} className="text-[10px] font-black uppercase tracking-tighter">Issue Date</p>
                            <p style={{ color: '#334155' }} className="text-sm font-bold">{visitor.date}</p>
                        </div>
                        <div className="space-y-1 text-right">
                            <p style={{ color: '#94a3b8' }} className="text-[10px] font-black uppercase tracking-tighter">Assigned Area</p>
                            <div className="flex items-center justify-end gap-1">
                                <MapPin style={{ color: '#94a3b8' }} className="h-3 w-3" />
                                <p style={{ color: '#334155' }} className="text-sm font-bold">{visitor.department || "Main Office"}</p>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Secure Footer Bar */}
                <div
                    style={{ backgroundColor: '#0f172a', borderTop: '1px solid rgba(251, 191, 36, 0.3)' }}
                    className="py-4 text-center"
                >
                    <p style={{ color: '#94a3b8' }} className="text-[9px] font-bold uppercase tracking-[0.2em]">
                        Security Verified &bull; Official Document
                    </p>
                </div>
            </div>

            {/* Premium Control Center */}
            <div className="flex gap-3 w-full max-w-[340px]">
                <Button onClick={downloadPass} className="flex-1 h-12 bg-blue-600 hover:bg-blue-700 text-white shadow-lg shadow-blue-500/30 rounded-xl font-bold transition-all hover:-translate-y-0.5" variant="default">
                    <Download className="mr-2 h-5 w-5" />
                    Save Digital Pass
                </Button>
                <Button variant="outline" className="h-12 w-12 rounded-xl border-slate-200 hover:bg-slate-50 shadow-sm" onClick={() => window.print()}>
                    <Printer className="h-5 w-5 text-slate-600" />
                </Button>
            </div>
        </div>
    )
}
